import Vue from 'vue'
import Router from 'vue-router'
import Lightt from '@/components/Lightt'

import '../styles/golbal.css'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Lightt',
      component: Lightt
    }
  ]
})



